@import "src/styles/Mixins";
@import "src/styles/fonts";

.animalType {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 16px;
  @include size(214px, 84px);
  background-color: white;
  margin: 24px 0;
  border-radius: 5px;
  cursor: pointer;
  padding: 5px 5px 5px 0;

  & span {
    @include SFDisplaySemibold();
    font-size: 16px;
    line-height: 24px;
    color: #202223;

  }
.animalTypeImageWrapper{
  @include size(85px,81px);
  & img{
    @include size(100%, 100%);
    object-fit: contain;
  }
}
}
.animalType:hover{
  & span{
    @include SFDisplaySemibold();
    font-size: 16px;
    line-height: 24px;
    color: #00A0AC;
  }
  &:hover{
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.06), 0 12px 32px rgba(0, 0, 0, 0.1);
  }
}
.active{
  border: 2px solid #008060;
}
.restTypes{
  opacity: 0.6;
}
@media (max-width: 768px) {
  .animalType{
    width: 137.88px;
    height: 56px;
    & .animalTypeImageWrapper{
      width: 56.88px;
      height: 53px;
    }
  }
}